import React, { useState } from 'react';
import emailjs from 'emailjs-com';
import gsap from "gsap";
import { MotionPathPlugin } from 'gsap/MotionPathPlugin'
import paperPlane from "../Assets/paper-plane.svg";
import "../Styles/Contact.css";

gsap.registerPlugin(MotionPathPlugin)

function Contact() {

    const [disable, setDisable] = useState(false);

    function floatLabel(e) {
        e.target.classList.add('active');
    }

    function resetLabel(e) {
        if (e.target.value === '') {
            e.target.classList.remove('active');
        }
    }

    function sendEmail(e) {
        setDisable(true)
        e.preventDefault();

        let tl = gsap.timeline();
        tl.to('.btn-text', .5, { x: 120 })
            .to('.paper-plane', .5, { x: 0 });

        emailjs.sendForm('service_apzyxis', 'template_w4m759g', e.target, 'user_Pgmtn1fOzzBMCnRBXmIxM')
            .then((result) => {
                document.getElementById("contact-form").reset();
                [...document.querySelectorAll('.input')].map(element => (
                    element.classList.remove('active')
                ))
                // success timeline
                let successTl = gsap.timeline({ delay: 2 });
                successTl.to('.paper-plane', .5, { x: 120 })
                    .to('.success', .5, { scale: 1, opacity: 1 })
                    .to('.success', .5, { scale: 0, opacity: 1, delay: 1 })
                    .to('.btn-text', .5, { x: 0 })
                    // Reset
                    .to('.paper-plane', 0, { opacity: 0 })
                    .to('.paper-plane', 0, { x: "-150px" })
                    .to('.paper-plane', 0, { opacity: 1 })
                setDisable(false);
            }, (error) => {
                // error timeline
                let failureTl = gsap.timeline({ delay: 2 });
                failureTl.to('.paper-plane', .5, { x: 120 })
                    .to('.failure', .5, { scale: 1, opacity: 1 })
                    .to('.failure', .5, { scale: 0, opacity: 1, delay: 1 })
                    .to('.btn-text', .5, { x: 0 })
                    // Reset
                    .to('.paper-plane', 0, { opacity: 0 })
                    .to('.paper-plane', 0, { x: "-150px" })
                    .to('.paper-plane', 0, { opacity: 1 })
                setDisable(false);
            });

    }

    return <section className="contact flex">

        <div className="contact-title flex flex-column center">
            <div className="contact-info flex flex-column center text-center">
                <h1>Get in touch</h1>
                <p>Send me a message, or visit my github page for more projects.</p>
                <div className="flex">
                    <a className="flex center" href="https://www.linkedin.com/in/mo-elhaj/" target="_blank" rel="noopener noreferrer">
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 27 27"><path d="M4.98 3.5c0 1.381-1.11 2.5-2.48 2.5s-2.48-1.119-2.48-2.5c0-1.38 1.11-2.5 2.48-2.5s2.48 1.12 2.48 2.5zm.02 4.5h-5v16h5v-16zm7.982 0h-4.968v16h4.969v-8.399c0-4.67 6.029-5.052 6.029 0v8.399h4.988v-10.131c0-7.88-8.922-7.593-11.018-3.714v-2.155z" /></svg>
                    </a>
                    <a className="flex center" href="https://github.com/moelhaj" target="_blank" rel="noopener noreferrer">
                        <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="currentColor" className="bi bi-github" viewBox="0 0 16 16">
                            <path d="M8 0C3.58 0 0 3.58 0 8c0 3.54 2.29 6.53 5.47 7.59.4.07.55-.17.55-.38 0-.19-.01-.82-.01-1.49-2.01.37-2.53-.49-2.69-.94-.09-.23-.48-.94-.82-1.13-.28-.15-.68-.52-.01-.53.63-.01 1.08.58 1.23.82.72 1.21 1.87.87 2.33.66.07-.52.28-.87.51-1.07-1.78-.2-3.64-.89-3.64-3.95 0-.87.31-1.59.82-2.15-.08-.2-.36-1.02.08-2.12 0 0 .67-.21 2.2.82.64-.18 1.32-.27 2-.27.68 0 1.36.09 2 .27 1.53-1.04 2.2-.82 2.2-.82.44 1.1.16 1.92.08 2.12.51.56.82 1.27.82 2.15 0 3.07-1.87 3.75-3.65 3.95.29.25.54.73.54 1.48 0 1.07-.01 1.93-.01 2.2 0 .21.15.46.55.38A8.012 8.012 0 0 0 16 8c0-4.42-3.58-8-8-8z" />
                        </svg>
                    </a>
                </div>
            </div>
        </div>

        <div className="contact-form flex center">


                <form id="contact-form" onSubmit={sendEmail}>

                    <div className="flex flex-column w-100">

                        <div className="input-box position-relative">
                            <input className="input" type="text" name="from_name" onFocus={floatLabel} onBlur={resetLabel} required />
                            <label className="label" htmlFor="from_name">Name</label>
                        </div>

                        <div className="input-box position-relative">
                            <input className="input" type="email" name="from_email" onFocus={floatLabel} onBlur={resetLabel} required />
                            <label className="label" htmlFor="from_email">Email</label>
                        </div>

                        <div className="input-box position-relative">
                            <textarea className="input" name="message" cols="30" rows="1" onFocus={floatLabel} onBlur={resetLabel} required ></textarea>
                            <label className="label" htmlFor="message">Message</label>
                        </div>

                        <div className="flex center">
                            <button className="btn flex center" disabled={disable}>
                                <div className="overflow-hidden position-relative w-100 h-100 flex center">

                                    {/* Failure animation */}
                                    <svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" fill="var(--white)" className="failure bi bi-x" viewBox="0 0 16 16">
                                        <path d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z" />
                                    </svg>

                                    {/* Success animation */}
                                    <svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" fill="var(--white)" className="success bi bi-check2" viewBox="0 0 16 16">
                                        <path d="M13.854 3.646a.5.5 0 0 1 0 .708l-7 7a.5.5 0 0 1-.708 0l-3.5-3.5a.5.5 0 1 1 .708-.708L6.5 10.293l6.646-6.647a.5.5 0 0 1 .708 0z" />
                                    </svg>

                                    {/* paper-plane */}
                                    <img src={paperPlane} className="paper-plane" alt="contact animation" />

                                    <div className="btn-text">Send</div>
                                </div>
                            </button>
                        </div>

                    </div>
                </form>



        </div>
    </section >
}

export default Contact;