import React, { useEffect } from 'react';
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import "../Styles/Projects.css";

gsap.registerPlugin(ScrollTrigger);

const projects = [
    {
        title: 'Bug Tracker',
        tags: ['Node.js', 'Express.js', 'React', 'MongoDB'],
        details: 'Managing projects, collaborating with teammate. Record bugs in your projects, assign them to teammates.',
        github: 'https://github.com/moElhaj/mern-bug-tracker',
        demo: 'https://mo-bug-tracker.herokuapp.com',
    },
    {
        title: 'Expenses Tracker',
        tags: ['React Native'],
        details: 'Track your finances to know where your money is going. manage transactions and Keep tabs on your spending.',
        github: 'https://github.com/moElhaj/expenses-tracker-react-native',
        demo: 'https://expo.dev/@moelhaj/expensya',
    },
    {
        title: 'ML cars prices',
        tags: ['Jupyter', 'Python'],
        details: 'Estimate cars prices based on features specified by you using Machine Learning.',
        github: 'https://github.com/moElhaj/Machine-Learning-Car-Estimator',
        demo: 'https://codex-garage.herokuapp.com',
    },
    {
        title: 'Employee Management',
        tags: ['PHP', 'MySql'],
        details: 'Payrolls and Human Resources Management.',
        github: 'https://github.com/moElhaj/php-employees-management',
    },
    {
        title: 'Task Management',
        tags: ['ASP.NET'],
        details: 'Manage tasks, sort by priority and flow due dates.',
        github: 'https://github.com/moElhaj/aspnet-task-management',
    }
];

function Projects() {

    useEffect(() => {
        gsap.to('.project', 1, {
            scrollTrigger: {
                trigger: '.projects',
                start: "top 70%",
                end: 'bottom 25%'
            },
            opacity: 1,
            y: 0,
            stagger: .3
        });
    }, [])

    return <section className="projects flex center">

        <div className="container p20 flex flex-column">

            <h1 style={{ marginBottom: '20px' }}>Projects</h1>

            <div className="flex projects-cards v-center">
                {projects.map((project, index) => (
                    <div key={index} className="project flex">

                        <div className="project-inner flex center">
                            <div className="project-inner-btn">
                                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="var(--black)" className="bi bi-grip-horizontal" viewBox="0 0 16 16">
                                    <path d="M2 8a1 1 0 1 1 0 2 1 1 0 0 1 0-2zm0-3a1 1 0 1 1 0 2 1 1 0 0 1 0-2zm3 3a1 1 0 1 1 0 2 1 1 0 0 1 0-2zm0-3a1 1 0 1 1 0 2 1 1 0 0 1 0-2zm3 3a1 1 0 1 1 0 2 1 1 0 0 1 0-2zm0-3a1 1 0 1 1 0 2 1 1 0 0 1 0-2zm3 3a1 1 0 1 1 0 2 1 1 0 0 1 0-2zm0-3a1 1 0 1 1 0 2 1 1 0 0 1 0-2zm3 3a1 1 0 1 1 0 2 1 1 0 0 1 0-2zm0-3a1 1 0 1 1 0 2 1 1 0 0 1 0-2z" />
                                </svg>
                            </div>
                            <div className="flex v-center project-links">
                                {project.demo && <a className="flex v-center project-link" href={project.demo} target="_blank" rel="noopener noreferrer">Demo</a>}
                                {project.github && <a className="flex v-center project-link" href={project.github} target="_blank" rel="noopener noreferrer">Code</a>}
                            </div>
                        </div>

                        <div className="project-info flex flex-column p20">
                            <h3>{project.title}</h3>
                            <p>{project.details}</p>
                            <div className="tags flex">
                                {project.tags.map((tag, index) => (
                                    <div key={index} className="tag">{tag}</div>
                                ))}
                            </div>
                        </div>
                        <div style={{ flexGrow: 1 }}></div>
                    </div>
                ))}
            </div>

        </div>

    </section>
}

export default Projects;